<template>
    <div class="mypage-container">
        <div class="mypage-title pc-element">계정정보</div>
        <div class="profile-box">
            <input type="file" style="display: none;" ref="img" accept="image/*" @change="imgUpload"/>
            <div class="img-box">
                <img v-if="memImg" :src="memImg">
                <font-awesome-icon v-else class="icon" icon="fa-solid fa-user"/>
            </div>
            <div class="user-name-box">
                <div class="user-name">{{uid}}</div>
                <v-btn class="whiteButton" depressed @click="userImageOpen()"><span>프로필 사진변경</span></v-btn>
            </div>
        </div>
        <form novalidate @submit.prevent="validate">
            <ul class="input-ul">
                <li>
                    <div class="label" style="padding-top:22px;">구분</div>
                    <div class="radio-box">
                        <v-radio-group
                                v-model="role"
                                dense
                                row
                                v-if="!isAdmin"
                        >
                            <v-radio
                                    color="#000"
                                    label="개인회원"
                                    disabled
                                    value="indi"
                            ></v-radio>
                            <v-radio
                                    color="#000"
                                    label="기업회원"
                                    disabled
                                    value="busi"
                            ></v-radio>
                        </v-radio-group>
                        <div v-else style="padding:22px 0 20px 0;font-weight:bold;">
                            관리자
                        </div>
                    </div>
                </li>
                <li>
                    <div class="label">법인명</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                readonly
                                filled
                                v-model="busiName"/>
                    </div>
                </li>
                <li>
                    <div class="label">아이디</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                readonly
                                filled
                                v-model="uid"/>
                    </div>
                </li>
                <li>
                    <div class="label">실명</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                readonly
                                filled
                                v-model="name"/>
                    </div>
                </li>
                <li>
                    <div class="label">이메일</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                readonly
                                filled
                                v-model="email"/>
                    </div>
                </li>
                <li v-if="!isAdmin">
                    <div class="label">닉네임</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                :rules="[nickErrors]"
                                placeholder="닉네임을 입력해주세요"
                                v-model="nick"/>

                        <div class="error-message" v-if="nickDouble">중복된 닉네임 입니다</div>
                    </div>
                </li>
                <li v-if="!isAdmin">
                    <div class="label">이메일 수신동의</div>
                    <div class="switch-box">
                        <div class="tip">
                            하모니카에서 주최하는 다양한 이벤트, 정보성 뉴스레터 및 광고 수신을 동의합니다.
                        </div>
                        <div class="switch">
                            <v-switch
                                    v-model="emailSend"
                                    value="yes"
                                    inset
                                    color="#6446C2"
                            ></v-switch>
                        </div>
                    </div>
                </li>

            </ul>
            <div class="button-row" v-if="!isAdmin">
                <v-btn type="submit" class="blackButton">
                    <span>저장</span>
                </v-btn>
            </div>
        </form>
        <div class="sns-box" v-if="snsList.length>0">
            <div class="label">소셜계정 연동</div>
            <ul class="sns-ul">
                <li class="sns-li" v-for="sns in snsList" :key="sns.code">
                    <div class="img-box">
                        <img :src="sns.img"><span>{{sns.title}}</span>
                    </div>
                    <div class="switch">
                        <v-switch
                                v-model="sns.active"
                                value="yes"
                                inset
                                color="#6446C2"
                        ></v-switch>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_MEMBER_INFO,
        API_URL_GET_MYPAGE_CHECK_NICK,
        API_URL_UPDATE_MEMBER_IMAGE,
        API_URL_UPDATE_MYPAGE_MEMBER,
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import {mapActions, mapGetters} from "vuex";

    export default {
        name: 'memberModify',
        computed: {
            ...mapGetters('config', ["getSns"]),
        },
        data() {
            return {
                uid: 'ade111',
                name: '',
                nick: '',
                nickError: false,
                nickDouble: false,
                emailSend: '',
                role: '',
                busiName: '',
                memImg: '',
                memberInfo:{},
                email: '',
                isAdmin: false,
                snsList: [],
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.memberInfo = member.memberInfo;
                this.getData();
            } else {
                this.$router.push('/');
            }

        },
        methods: {
            ...mapActions('config', ["setMemberImg"]),
            settingSns() {
                this.snsList = [];
                for (const key in this.getSns) {
                    let img = '';
                    let title = '';
                    switch (key) {
                        case 'gl':
                            img = require('@/assets/img/ico_sns1.svg')
                            title = '구글로 회원가입'
                            break;
                        case 'fb':
                            img = require('@/assets/img/ico_sns2.svg')
                            title = '페이스북으로 회원가입'
                            break;
                        case 'ka':
                            img = require('@/assets/img/ico_sns4.svg')
                            title = '카카오톡으로 회원가입'
                            break;
                        case 'nv':
                            img = require('@/assets/img/ico_sns3.svg')
                            title = '네이버로 회원가입'
                            break;


                    }
                    if (this.getSns[key].key) {
                        let item = {
                            img: img,
                            code: key,
                            title: title,
                            active: '',
                        }
                        this.snsList.push(item);
                    }
                }
            },
            getData() {
                apiService.post(API_URL_GET_MEMBER_INFO, {}, (res) => {

                    if (res.status == 'success') {
                        const memberInfo = res.memberInfo;
                        this.uid = memberInfo.uid;
                        this.nick = memberInfo.nick;
                        this.name = memberInfo.name;
                        this.email = memberInfo.email;
                        this.emailSend = memberInfo.emailSend;

                        switch (memberInfo.role) {
                            case 'ROLE_ADMIN':
                                this.isAdmin = true;
                                break;
                            case 'ROLE_MANAGER':
                                this.role = 'busi';
                                this.busiName = memberInfo.busiName
                                break;
                            case 'ROLE_MEMBER':
                                this.role = 'indi';
                                this.settingSns();
                                break;
                        }
                        if (memberInfo.img) {
                            this.memImg = apiService.imgHost + 'api/controller/member/getMemberImage?imgName=' + encodeURIComponent(memberInfo.img)
                        }


                    }
                })
            },
            nickErrors() {
                this.nickError = false;
                this.nickDouble = false;
                if (this.nick) {
                    const reg = /^[ㄱ-ㅎ|가-힣|a-z|A-Z|0-9|]{2,10}$/;
                    if (!reg.test(this.nick)) {
                        this.nickError = true;
                        return '닉네임은 한글,영문,숫자 2~10자 만 가능합니다';
                    } else {
                        const response = apiService.asyncPost(API_URL_GET_MYPAGE_CHECK_NICK, {nick: this.nick});
                        response.then((res) => {
                            console.log(res.data.status)
                            switch (res.data.status) {
                                case 'fail':
                                case 'message':
                                    this.nickDouble = true;
                                    break;
                                case 'success':
                                    this.nickDouble = false;
                                    break;
                            }
                        })
                    }
                }
                return true;
            },
            userImageOpen() {
                this.$refs.img.click();
            },
            imgUpload() {
                const imgObj = this.$refs.img.files;
                let img = imgObj.length > 0 ? imgObj[0] : null;
                const frm = new FormData();
                frm.append('image', img);
                apiService.postFile(API_URL_UPDATE_MEMBER_IMAGE, frm, (res) => {
                    if (res.status == 'success') {
                        this.memImg = apiService.imgHost + 'api/controller/member/getMemberImage?imgName=' + encodeURIComponent(res.memImg)
                        this.memberInfo.img = res.memImg;
                        sessionStorage.setItem('memberInfo', JSON.stringify(this.memberInfo))
                        this.setMemberImg(res.memImg);
                    }
                })
                this.$refs.img.value = '';

            },
            validate() {
                if (this.nick) {
                    if (this.nickDouble || this.nickError) {
                        return;
                    }
                }
                let frm = {
                    emailSend: this.emailSend,
                    nick: this.nick,
                }
                if (this.serverFlag) return;
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_UPDATE_MYPAGE_MEMBER, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success') {
                        params = {
                            message: '회원정보가 변경되었습니다',
                        };

                    } else {
                        params = {
                            message: '중복된 닉네임 입니다',
                        };
                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            }
        }

    }
</script>


<style lang="scss" scoped>
    .mypage-container {
        width: 100%;
        margin: 0;
        padding: 21px 24px 70px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .mypage-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .button-row {
            width: 86px;
            margin: 40px auto 10px auto;

            .blackButton {
                max-height: 38px !important;

                span {
                    font-size: 14px;
                }
            }

        }

        .profile-box {
            padding: 50px 0 30px 0;
            text-align: center;
            border-bottom: solid 1px #ddd;

            .img-box {
                width: 96px;
                height: 96px;
                overflow: hidden;
                border: solid 1px #ddd;
                border-radius: 50%;
                margin: auto;

                .icon {
                    font-size: 40pt;
                    color: #ddd;
                    margin-top: 18px;
                }

                img {
                    width: 100%;
                    min-height: 96px;
                }
            }

            .user-name {
                font-size: 18px;
                color: #333;
                text-align: center;
                font-weight: 600;
                margin: 5px 0;
            }

            .whiteButton {
                width: 118px;
                height: 36px;
                margin: auto;
                max-height: 36px;

                span {
                    font-size: 13px !important;
                }
            }
        }

        .input-ul {
            padding: 0 20px;
            margin: 10px 0 0 0;

            li {
                display: flex;
                justify-content: space-between;

                .label {
                    min-width: 120px;
                    padding: 15px 0 0 0;
                    font-weight: 600;
                    color: #333;
                    font-size: 14px;

                }

                .input-box {
                    text-align: left;
                    width: 95%;
                    position: relative;

                    .error-message {
                        position: absolute;
                        bottom: 8px;
                        left: 10px;
                        color: #880E4F;
                        font-size: 12px;
                    }
                }

                .radio-box {
                    text-align: left;
                    width: 95%;
                    padding-bottom: 10px;
                }

                .switch-box {
                    width: 95%;
                    display: flex;
                    justify-content: space-between;

                    .tip {
                        width: 99%;
                        font-size: 12px;
                        color: #333333;
                        padding-top: 15px;
                    }

                    .switch {
                        text-align: right;
                        min-width: 30px;
                        margin-top: -6px;
                    }
                }
            }
        }

        .sns-box {
            margin: 50px 0 10px 0;
            border-top: solid 1px #ddd;
            display: flex;
            justify-content: space-between;
            padding: 30px 20px 0 20px;

            .label {
                min-width: 120px;
                padding: 15px 0 0 0;
                font-weight: 600;
                color: #333;
                font-size: 14px;

            }

            .sns-ul {
                width: 95%;
                padding: 0;
                margin: 0;

                li {
                    width: 100%;
                    height: 48px;
                    border: solid 1px #dcdcdc;
                    border-radius: 5px;
                    margin: 10px 0;
                    text-align: center;
                    line-height: 48px;
                    display: flex;
                    justify-content: space-between;

                    .img-box {
                        padding-left: 20px;

                        img {
                            margin: 8px 10px 0 0;
                        }

                        span {
                            position: relative;
                            top: -8px;
                            font-size: 18px;
                            color: #333;
                            font-weight: bold;

                        }
                    }

                    .switch {
                        text-align: right;
                        min-width: 30px;
                        margin-top: -6px;
                        padding-right: 7px;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 1259px) {
        .mypage-container {
            padding: 0;

            .profile-box {
                padding: 30px 0;
                display: flex;

                .img-box {
                    margin-left:10px;
                    min-width: 96px;
                }
                .user-name-box {
                    width:90%;
                    text-align: left;
                    padding-left:30px;
                    .user-name {
                        font-size: 18px;
                        color: #333;
                        text-align: left;
                        font-weight: 600;
                        margin: 5px 0;
                    }

                    .whiteButton {
                        width: 118px;
                        height: 36px;
                        margin: auto;
                        max-height: 36px;

                        span {
                            font-size: 13px !important;
                        }
                    }
                }
            }
            .input-ul {
                padding: 0;
                margin: 10px 0 0 0;

                li {
                    display: block;
                    .label {
                        padding:0 0 10px 0;
                    }

                    .input-box {
                        width:100%;

                    }

                    .radio-box {
                        text-align: left;
                        width:100%;
                        padding-bottom: 10px;
                    }

                    .switch-box {
                        width:100%;
                        display: flex;
                        justify-content: space-between;

                        .tip {
                            width: 99%;
                            font-size: 12px;
                            color: #333333;
                            padding-top: 15px;
                        }

                        .switch {
                            text-align: right;
                            min-width: 30px;
                            margin-top: -6px;
                        }
                    }
                }

            }
            .sns-box {
                display: block;
                padding: 30px 20px 0 20px;

                .label {
                    font-size:16px;

                }

                .sns-ul {
                    width: 100%;
                    li {
                        .img-box {
                            span {
                                top: -10px;
                                font-size: 14px;

                            }
                        }


                    }
                }
            }
        }
    }
    @media screen and (max-width: 450px) {
        .mypage-container {
            padding: 0 20px;


        }
    }
</style>
